import React from "react";
import styles from "./TeamSettings.module.css";
import { useHistory } from "react-router-dom";

const TeamSettings = (props) => {
  let history = useHistory();
  return (
    <div className={styles.containerTeamSettings}>
      <div className="FillContainer">
        <button className="Selected">Team Settings</button>
        <button
          onClick={
            (e) => {
              e.preventDefault();
              history.push("/app");
            }
            // window.location.href = "#/app";}
          }
        >
          App Settings
        </button>
      </div>

      <div className="TableContainer">
        <table>
          <tr>
            <td>
              <span>Team Name</span>
            </td>
            <td>
              <select>
                <option>Team names</option>
              </select>
            </td>
          </tr>
          <tr>
            <td>
              <span>Number of Players</span>
            </td>
            <td>
              <select>
                <option>1</option>
                <option>2</option>
                <option>3</option>
                <option>4</option>
              </select>
            </td>
          </tr>
          <tr>
            <td>
              <span>Difficulty</span>
            </td>
            <td>
              <select>
                <option>Normal</option>
                <option>Veteran</option>
              </select>
            </td>
          </tr>
        </table>
      </div>

      <div className="TableContainer">
        <table>
          <tr>
            <td>
              <span>Player 1 Name</span>
            </td>
            <td>
              <input></input>
            </td>
          </tr>
          <tr>
            <td>
              <span>Player 1 Character</span>
            </td>
            <td>
              <select>
                <option>1</option>
                <option>2</option>
                <option>3</option>
                <option>4</option>
              </select>
            </td>
          </tr>
        </table>
      </div>

      <div className="FillContainer">
        <div>
          <input type="checkbox" checked="true"></input>
          <label>Include legacy items</label>
        </div>

        <button
          className="EvenMargin"
          onClick={(e) => {
            e.preventDefault();
            history.push("/");
            // window.location.href = "#/";
          }}
        >
          Edit Decks
        </button>
      </div>
      <div className="FillContainer">
        <button className="EvenMargin">Export/Import</button>

        <button className="EvenMargin">Dials</button>
      </div>
      <div className="FillContainer">
        <button className="EvenMargin">Delete Team</button>

        <button className="EvenMargin">Create new team</button>
      </div>
    </div>
  );
};

export default TeamSettings;
