import React from "react";
import { Link } from "react-router-dom";

const Cards = (props) => {
  return (
    <div className="containerEditDecks">
      <div className="BlockContainer">
        <div style={{ display: "flex", maxheight: 50 }}>
          <Link to="/team">
            <img className="ButtonIcon" src={"./Icons/T_23_gear_.png"} />
          </Link>

          <p>Team: Team name</p>
        </div>
        <div className="Right">
          <img
            className="Right ButtonIcon"
            src={"./Icons/T_17_medal_star_.png"}
          />
          <img className="Right ButtonIcon" src={"./Icons/bonus_07.png"} />
        </div>
      </div>

      <div className="BlockContainer">
        <div style={{ display: "flex" }}>
          <img className="ButtonIcon flip" src={"./Icons/bonus_04.png"} />

          <select>
            <option>Selected Player</option>
          </select>
        </div>
        <div className="Right">
          <img
            className="Right ButtonIcon"
            src={"./Icons/T_7_head_scull_enemy_.png"}
          />
          <img className="Right ButtonIcon" src={"./Icons/T_9_flag_.png"} />
        </div>
      </div>

      <div className="row">
        <div className="column">
          <p>Avilable Cards</p>
          <ul className="CardsList">
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
          </ul>
        </div>
        <div className="column">
          <p>Cards in deck</p>
          <ul className="CardsList">
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
            <li className="Card">Card</li>
          </ul>
        </div>
      </div>
      <div className="row">
        <div className="column">
          <div>
            <input type="radio" id="rarity" name="Sort" value="rarity" />
            <label for="rarity">Sory by Rarity</label>
            <br />
          </div>
          <div>
            <input type="radio" id="name" name="Sort" value="name" />
            <label for="name">Sort by Name</label>
            <br />
          </div>
          <button>Reset Deck</button>
        </div>
        <div className="column">
          <div>
            <input type="radio" id="allcards" name="filter" value="allcards" />
            <label for="allcards">All Cards</label>
            <br />
          </div>
          <div>
            <input
              type="radio"
              id="cardstoremove"
              name="filter"
              value="cardstoremove"
            />
            <label for="cardstoremove">Cards to remove</label>
            <br />
          </div>
          <div>
            <input
              type="radio"
              id="equipmentcards"
              name="filter"
              value="equipmentcards"
            />
            <label for="equipmentcards">Equipent Cards</label>
            <br />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Cards;
